import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-skills',
  templateUrl: './skills.component.html',
  styleUrls: ['./skills.component.css']
})
export class SkillsComponent implements OnInit {
  show = true;

  constructor() { }

  ngOnInit(): void {
  }

  next(): void {
    if (this.show){
      this.show = false;
    } else{
      this.show = true;
    }
  }
}
